  get currentActions() {
    let pa = this.reviewList;
    if (this.mode === 'deal') {
      pa = this.actionList.filter(
        (item) => this.permissionActions.indexOf(item['title']) > -1
      );
      pa.unshift(...this.dealList);
    }
    pa.push(...this.defaultList);
    return pa;
  }

  // 任务详情信息查询
  getTaskDetail(taskId: any, diffTry: any) {
    this.loading = true;
    if (this.mode === 'review') {
      const obj = {
        id: this.id
      };
      taskInfo(obj).then(({ data, error }) => {
        this.loading = false;
        if (!error) {
          this.dataInfo(data, diffTry);
        } else {
          this.$message.error(error.message);
        }
      });
    } else {
      queryTaskDetail({ taskId }).then(({ data, error }) => {
        this.loading = false;
        if (!error) {
          this.dataInfo(data, diffTry);
        } else {
          this.$message.error(error.message);
        }
      });
    }
  }
  dataInfo(data, diffTry) {
    this.taskDetail = data;
    console.log(this.taskDetail.formData);
    if (!this.processInstanceId) {
      this.processInstanceId = data.processInstanceId;
      this.processDefinitionId = data.processDefinitionId;
      this.taskDefinitionKey = data.taskDefinitionKey;
    }
    if (data.processedForms) {
      const formAttribute = JSON.parse(Base64.decode(data.processedForms));
      if (!formAttribute.labelSuffix === undefined) {
        formAttribute.labelSuffix = ':';
      }
      if (formAttribute.labelWidth === undefined) {
        formAttribute.labelWidth = '120';
      }
      if (!formAttribute.labelPosition === undefined) {
        formAttribute.labelPosition = 'right';
      }
      formAttribute.formList.forEach((item) => {
        item['valueSymbol'] = '';
        item['visibleFrom'] = false;
        item['tableData'] = [];
        // 临时代码，清除历史错误数据
        if (item.showFrom) {
          delete item.showFrom;
        }
      });
      // 控制表单显示与隐藏
      this.templateFormsChange(data, formAttribute);
      this.formAttribute = formAttribute;
      // 刷新表单
      // if (diffTry === 'tryAgain') {
      //   this.previewFormRef.renderView(formAttribute);
      // }
    }
    if (
      data.inputParameters &&
      data.inputParameters.length > 0 &&
      data.inputParameters[0].operationAuthority
    ) {
      this.permissionActions = data.inputParameters[0].operationAuthority;
    }
  }
  templateFormsChange(data, formAttribute) {
    // console.log(data['formData'])
    // console.log(data['templateForms']);
    if (data.templateForms) {
      formAttribute.formList &&
        formAttribute.formList.forEach((item, index) => {
          const conf = arrayFind(data.templateForms, 'formItem', item.key);
          if (conf) {
            item.editable = conf.editable;
            item.readable = conf.readable;
          }
          if (item.type === 'table') {
            if (conf) {
              item.editable = conf.editable;
              item.readable = conf.readable;
              if (conf.extendProp) {
                const extendPropList = JSON.parse(
                  Base64.decode(conf.extendProp)
                );

                if (extendPropList) {
                  const colsList: Array<any> = [
                    {
                      type: 'index',
                      label: '序号',
                      align: 'center',
                      width: '50px'
                    }
                  ];
                  item.cols.forEach((itemCol: any) => {
                    const col = arrayFind(
                      extendPropList,
                      'formItem',
                      itemCol.prop
                    );
                    if (col) {
                      if (col.editable === 1 || col.readable === 1) {
                        itemCol.hidden = false;
                      } else {
                        itemCol.hidden = true;
                      }
                      colsList.push({
                        ...itemCol,
                        editable: col.editable,
                        readable: col.readable,
                        'min-width':
                          itemCol.label.length > 10
                            ? '220px'
                            : itemCol.label.length > 8
                            ? '180px'
                            : itemCol.label.length > 6
                            ? '170px'
                            : itemCol.label.length > 4
                            ? '140px'
                            : '120px',
                        'show-overflow-tooltip': true,
                        'header-align': 'left',
                        align: 'left'
                      });
                    }
                  });
                  item.cols = colsList;
                }
              }
            }
          }
          if (item.type === 'childFrom') {
            item.value &&
              item.value.formList &&
              item.value.formList.forEach((citem) => {
                citem.editable = 1;
                citem.readable = 0;
              });
            data['formData'] &&
              this.templateFormsChange(
                { formData: data['formData'][item.key], templateForms: [] },
                item.value
              );
          }

          if (this.mode === 'review') {
            if (item.editable === 1) {
              item.readable = 1;
              item.editable = 0;
            }
          }

          if (data.formData) {
            // 回显历史赋值
            if (item.type === 'table') {
              item.datas = data.formData[item.key];
            } else if (item.type !== 'childFrom') {
              item.value = data.formData[item.key];
            }
          }
          if (item.refreshLoading) {
            item.datas = [];
            item.value = null;
          }
          // 合并操作列
          if (item.type === 'table') {
            const buttonCol: Array<any> = [];
            const buttonList: Array<any> = [];
            let colBoolean = false;
            item.cols.forEach((itemCol) => {
              if (itemCol.type === 'button') {
                if (itemCol.editable === 1) {
                  buttonCol.push(itemCol);
                }
              }
              if (itemCol.type !== 'button') {
                buttonList.push(itemCol);
              }
              if (
                itemCol.fieldCode === 'operation' &&
                itemCol.fieldName === '操作'
              ) {
                colBoolean = true;
              }
            });
            item.cols = buttonList;
            if (
              colBoolean === false &&
              (buttonCol.length > 0 || (item.editable === 1 && item.addLine))
            ) {
              item.cols.push({
                prop: 'operation',
                editable: false,
                align: 'left',
                label: '操作',
                minwidth: '100',
                buttonAdd: buttonCol
              });
            }
          }
        });
    }
  }
  // 任务流转详情
  history() {
