  successList(response, file, fileList) {
    if (response.data && response.data.path) {
      this.fileList = [];
      fileList.forEach((item: any) => {
        this.fileList.push({
          name: item.name,
          url: item.response['data']['path'],
          uid: item.uid
        });
      });
    } else {
      this.$message.warning('文件上传失败:' + response.message);
    }
  }
  vaildForm() {
    let vaild = true;
    if (this.formAttribute.formList) {
      const tempArr = this.formAttribute.formList.filter((item) => {
        return (!item.showFrom || item.showFrom === 'show') && item.editable;
      });
      if (
        tempArr.some(
          (item) => item.type !== 'table' && item.required && !item.value
        )
      ) {
        this.$message.warning('请先完成必填表单项输入');
        vaild = false;
      }
    }
    return vaild;
  }

  paramsObtain() {
    const param = {};
    // 不校验当前页面隐藏（editable为1），但标记为必输的表单项字段
    if (Object.prototype.hasOwnProperty.call(this.formAttribute, 'formList')) {
      this.formAttribute.formList.forEach((item: any) => {
        // 只保存显示的表单项值
        if (
          (!item.showFrom || item.showFrom === 'show') &&
          (item.editable === 1 || item.readable === 1)
        ) {
          if (item.type === 'table') {
            // 删除编辑状态
            if (item.datas) {
              item.datas.forEach((item) => {
                delete item['edits'];
                delete item['beModified'];
              });
              this.$set(param, item.key, item.datas);
            }
          } else if (item.type === 'childFrom') {
            const childParam = {};
            item.value.formList.forEach((itemChild) => {
              if (itemChild.type === 'table') {
                if (itemChild.datas) {
                  itemChild.datas.forEach((item) => delete item['edits']);
                  this.$set(childParam, itemChild.key, itemChild.datas);
                }
              } else {
                this.$set(childParam, itemChild.key, itemChild.value);
              }
            });
            this.$set(param, item.key, childParam);
          } else if (Object.prototype.hasOwnProperty.call(item, 'realValue')) {
            this.$set(param, item.key, item.realValue);
          } else {
            this.$set(param, item.key, item.value);
          }
        }
      });
    }
    return {
      files: this.fileList,
      formData: param,
      remark: this.remarks,
      taskId: this.taskId,
      editMarks: this.previewFormRef.editMarks
    }; // 表单参数
  }
  // 通过
  adopt() {
    if (this.vaildForm()) {
      this.loading = true;
      completeTask(this.paramsObtain()).then(({ data, error }) => {
        this.loading = false;
        if (error) {
          this.$message.error(error.message);
        } else {
          this.$message.success('通过操作成功');
          this.cancle();
        }
      });
    }
  }
