  // 审阅
  review() {
    const params = {
      files: this.fileList,
      remark: this.remarks,
      taskId: this.taskId,
      id: this.taskDetail.id
    }; // 表单参数
    this.loading = true;
    dealReview(params).then((res: any) => {
      this.loading = false;
      if (res.error) {
        this.$message.error('审阅失败：' + res.error.message);
      } else {
        this.$message.success('审阅成功');
        this.cancle();
      }
    });
  }
